body, html {
    margin: 0; padding: 0;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    background: #f4f6f8;
    color: #333;
  }
  
  .container {
    max-width: 900px;
    margin: 30px auto;
    background: white;
    padding: 20px 30px;
    border-radius: 8px;
    box-shadow: 0 2px 8px rgba(0,0,0,0.1);
  }
  
  h1, h2, h3 {
    color: #222;
    margin-bottom: 20px;
  }
  
  button {
    background: #007bff;
    color: white;
    border: none;
    padding: 10px 16px;
    border-radius: 4px;
    font-size: 1rem;
    cursor: pointer;
    transition: background 0.3s ease;
    margin-top: 20px;
  }
  
  button:hover {
    background: #0056b3;
  }
  
  input, select {
    padding: 8px 10px;
    border: 1px solid #ccc;
    border-radius: 4px;
    font-size: 1rem;
    width: 100%;
    margin-top: 6px;
    margin-bottom: 12px;
  }
  
  .table {
    width: 100%;
    border-collapse: collapse;
    margin-top: 14px;
  }
  
  .table th, .table td {
    border: 1px solid #ddd;
    padding: 10px;
    text-align: left;
  }
  
  .table th {
    background: #f0f0f0;
    font-weight: 600;
  }
  
  .table tr:nth-child(even) {
    background: #fbfbfb;
  }
  
  .navbar {
    background: #003366;
    padding: 10px 20px;
    color: white;
    font-weight: bold;
    font-size: 1.2rem;
    margin-bottom: 30px;
  }
  
  .footer {
    margin-top: 40px;
    text-align: center;
    color: #777;
    font-size: 0.9rem;
    padding: 16px 0;
  }
  
  @media (max-width: 600px) {
    .container {
      margin: 10px;
      padding: 15px 20px;
    }
  }
  .week-navigation {
    margin-bottom: 15px;
    display: flex;
    align-items: center;
  }
  
  .week-navigation button {
    background: #007bff;
    border: none;
    padding: 8px 14px;
    color: white;
    border-radius: 5px;
    cursor: pointer;
  }
  
  .week-navigation button:hover {
    background: #0056b3;
  }
  
  .date-range {
    margin: 0 15px;
    font-weight: 600;
    font-size: 1rem;
  }
  
  .table-container {
    overflow-x: auto;
    margin-bottom: 20px;
  }
  
  .table td.present {
    cursor: pointer;
    background-color: lightgreen;
    text-align: center;
  }
  
  .table td.absent {
    cursor: pointer;
    background-color: tomato;
    text-align: center;
  }
  
  .save-button {
    background-color: #008000;
    padding: 10px 20px;
    border-radius: 6px;
    border: none;
    color: white;
    font-weight: 600;
    cursor: pointer;
    transition: background-color 0.3s;
  }
  
  .save-button:hover {
    background-color: #005600;
  }
  
  